io a lo que conocemos como “Educación para la Salud”. La Educación para la Salud es dirigida a impactar la conducta, y tiene como fin generar cambios en los estilos de vida a partir de la implementación de comportamientos saludables. Para lograr esto, se articulan varios enfoques: el de creencias en salud, que parte de que son las creencias las que influyen en los comportamientos de las personas y en su aceptación de las medidas de salud recomendadas; y la comunicación persuasiva, basada en la CAP (conocimientos, actitudes y prácticas), donde las actitudes determinarán los comportamientos saludables.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"BG05 p-4 mb-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-2 col-4 px-4 mb-lg-0 mb-4",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema5/img16.svg"),"alt":"Imagen decorativa"}})]),_c('div',{staticClass:"col-lg-10",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"p mb-0"},[_vm._v("La "),_c('strong',[_vm._v("comunicación persuasiva")]),_vm._v(", llamada también difusionista, considera que el aportar conocimiento, por ejemplo, en temas de salud, hará que las personas cambien de actitud, y eso hará que cambien su comportamiento; el medio por excelencia de este enfoque son los medios de comunicación masiva.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"BG02 mb-5",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-1 col-2 pe-2 py-3 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/img05.svg")}})]),_c('div',{staticClass:"col p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Este enfoque da nacimiento al mercadeo social, la educación sanitaria, la promoción de la salud y el edu-entretenimiento. Con el