e las personas cambiarán su conducta en la medida que conozcan el daño que genera un comportamiento determinado. Se parte de que las personas se comportan de manera racional y que con una información sobre riesgos cambiarán su conducta. Este enfoque, entonces, busca influir sobre las creencias, aptitudes y comportamientos de las personas, por ejemplo, mediante las campañas publicitarias en medios de comunicación masiva, los cambios en los etiquetados y las medidas informativas y actitudinales. ")])]),_c('div',{staticClass:"col-lg-5 col-10 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema5/img17.png"),"alt":"Imagen decorativa"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-3 col-6 mb-lg-0 mb-4",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema5/img18.png"),"alt":"Imagen decorativa"}})]),_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("Tanto el enfoque netamente informativo como el enfoque difusionista orientado a la conducta tienen grandes limitaciones: no toman en cuenta las dinámicas locales ni las múltiples dimensiones socioeconómicas que atraviesan al sujeto, y tampoco los medios o formas de comunicación locales, parten de un modelo biológico individual de salud – enfermedad, ponen las problemáticas de salud en el comportamiento de los individuos, con poco o ningún interés por las cuestiones sociales, convirtiendo los comportamientos en el objetivo último de las intervenciones, adoptando una mirada vertical y autoritaria, donde se debe acatar la directriz del personal de salud. ")]),_c('div',{staticClass:"BG02 p-4 mb-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-2 col-4 px-4 mb-lg-0 mb-4",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{